.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px; 
  padding-bottom: 5px; 
}

.left-header h1 {
  margin: 0;
  font-size: 2em;
}

.left-header p {
  margin: 0;
  font-size: 1.2em; /* Adjust this font size as needed for the 'Data Scientist' text */
}

.right-header p {
  margin: 0;
  font-size: 1em;
} 


.title {
  color: gray;
}

.right-header p {
  margin: 0;
}

.section-title {
    font-size: 17px; /* Adjusted to match your desired font size */
    color: #555;
    font-style: italic;
    border-bottom: 2px solid #f1c40f; /* Yellow line */
    padding-bottom: 0px; /* Reduce padding under title */
    margin-bottom: 8px; /* Reduce margin under title */
}


.section-description {
  font-size: 1.1em;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
}

.skills-list .skill {
  margin-right: 10px;
  background-color: #f0f0f0;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 1.1em;
  margin-bottom: 10px;
}

.education-section .education-item {
  margin-bottom: 10px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
